.filterlist {
  width: 200px;
  margin: 15px;
}
.filterlist .filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.filterlist .delete-filter {
  margin-left: 5px;
  cursor: pointer;
}
.filterlist .btn-add-filter {
  width: 110px;
  margin-top: 10px;
  color: white !important;
  background-color: #437aa5 !important;
}
.filterlist .filter-holder {
  width: 185px;
  padding: 5px 10px;
  height: auto;
  background-color: #ececec;
}
.filterlist .filter-holder .text {
  white-space: normal !important;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  text-overflow: ellipsis;
}
.filter-holder-dropdown {
  max-width: 300px;
}
